"use client";

import { BookCopy, Lightbulb, TrendingUp, Laptop, Users, Award } from "lucide-react";
import { GlassCard, CardHeader, CardTitle, CardDescription } from "./glass-card";
import * as React from "react";
import { useGSAP, prefersReducedMotion } from "@/lib/gsap-utils";

const features = [
  {
    icon: <BookCopy className="h-12 w-12 text-white" />,
    title: "Structured Learning Path",
    description: "Step-by-step notes organized from beginner to advanced topics, ensuring a solid foundation.",
  },
  {
    icon: <Lightbulb className="h-12 w-12 text-white" />,
    title: "Clear & Concise",
    description: "No fluff. Just the essential information you need, explained in a simple and direct way.",
  },
  {
    icon: <TrendingUp className="h-12 w-12 text-white" />,
    title: "Always Updated",
    description: "Our notes are regularly refreshed to reflect the latest HTML standards and best practices.",
  },
  {
    icon: <Laptop className="h-12 w-12 text-white" />,
    title: "Easy Access",
    description: "Learn anytime, anywhere. Access your notes on any device with a web browser.",
  },
  {
    icon: <Users className="h-12 w-12 text-white" />,
    title: "Beginner-Friendly",
    description: "We craft our content specifically for those who are just starting their web development journey.",
  },
  {
    icon: <Award className="h-12 w-12 text-white" />,
    title: "Free High-Quality Resources",
    description: "Kickstart your learning with our extensive library of high-quality notes, completely free.",
  },
];

export default function KeyFeaturesSection() {
  const scopeRef = useGSAP(async (ctx) => {
    if (prefersReducedMotion()) return;
    const { gsap } = await import("gsap");
    await import("gsap/ScrollTrigger");

    const cards = ctx.selector?.("[data-feature-card]") as unknown as HTMLElement[];
    if (!cards || !cards.length) return;

    const isDesktop = typeof window !== 'undefined' && window.innerWidth >= 768;

    gsap.fromTo(
      cards,
      { opacity: 0, rotateY: isDesktop ? 90 : 0, y: isDesktop ? 0 : 20 },
      {
        opacity: 1,
        rotateY: 0,
        y: 0,
        duration: 0.8,
        ease: "power3.out",
        stagger: 0.15,
        scrollTrigger: {
          trigger: ctx.scope as Element,
          start: isDesktop ? "top top" : "top 80%",
          end: isDesktop ? "+=120%" : "top center",
          scrub: isDesktop ? 0.5 : false,
          pin: isDesktop,
        },
      }
    );
  });

  return (
    <section id="features" ref={scopeRef as any} className="w-full py-32 md:py-40 bg-gradient-to-b from-background to-gray-50">
      <div className="container px-4 md:px-6">
        <div className="flex flex-col items-center justify-center space-y-4 text-center mb-12">
          <div className="space-y-2">
            <h2 className="text-4xl font-bold tracking-tighter sm:text-5xl text-gradient">What Makes VoyLearning Different</h2>
            <p className="max-w-[900px] text-muted-foreground md:text-xl/relaxed lg:text-base/relaxed xl:text-xl/relaxed">
              Explore the features that make our platform the perfect starting point for your web development voyage.
            </p>
          </div>
        </div>
        <div className="mx-auto grid items-start gap-8 sm:max-w-4xl sm:grid-cols-2 md:gap-12 lg:max-w-5xl lg:grid-cols-3 [perspective:1000px]">
          {features.map((feature, index) => (
            <GlassCard key={index} className="text-center hover:rotate-[5deg] transition-transform" hover gradient data-feature-card>
              <CardHeader className="items-center">
                <div className="mb-4 flex h-16 w-16 items-center justify-center rounded-full motion-safe:animate-float" style={{ backgroundImage: 'var(--gradient-3)' }}>
                  {feature.icon}
                </div>
                <CardTitle>{feature.title}</CardTitle>
                <CardDescription>{feature.description}</CardDescription>
              </CardHeader>
            </GlassCard>
          ))}
        </div>
      </div>
    </section>
  );
}
