"use client";

import * as React from "react";
import { GlassCard, CardContent, CardTitle } from "./glass-card";
import { useGSAP, prefersReducedMotion } from "@/lib/gsap-utils";
import DotPattern from "./dot-pattern";
import { CheckCircle2, Cpu, Clock, Sparkles, ShieldCheck, TrendingUp } from "lucide-react";

const benefits = [
  { num: "01", title: "Personalized Learning", icon: Sparkles, desc: "Tailored adjustments to notes to match your understanding." },
  { num: "02", title: "AI-Powered Assistance", icon: Cpu, desc: "Ask for examples, simplification, or summaries instantly." },
  { num: "03", title: "Always Accessible", icon: ShieldCheck, desc: "Your edited notes are available anywhere, anytime." },
  { num: "04", title: "Save Time", icon: Clock, desc: "Skip the fluff. Focus on what matters for faster progress." },
  { num: "05", title: "Build Confidence", icon: CheckCircle2, desc: "Grow your skills with clear, editable content." },
  { num: "06", title: "Track Progress", icon: TrendingUp, desc: "Keep moving forward with visible improvements." },
];

export default function HorizontalScrollSection() {
  const scopeRef = useGSAP(async (ctx) => {
    if (prefersReducedMotion()) return;
    // Use ctx.selector to safely query within the registered scope
    const container = (ctx.selector?.('[data-hscroll]') as NodeListOf<HTMLElement> | undefined)?.[0] ?? null;
    if (!container) return;
    if (window.innerWidth < 768) return; // mobile fallback

    const items = Array.from(container.querySelectorAll<HTMLElement>("[data-card]"));
    if (!items.length) return;

    const { gsap } = await import("gsap");
    const { ScrollTrigger } = await import("gsap/ScrollTrigger");
    gsap.registerPlugin(ScrollTrigger);

    const totalWidth = container.scrollWidth;
    const xAmount = -(totalWidth - container.clientWidth);

    gsap.to(items, {
      x: xAmount,
      ease: "none",
      scrollTrigger: {
        trigger: container,
        pin: true,
        scrub: 1,
        start: "top top",
        end: () => `+=${Math.max(totalWidth, window.innerWidth)}`,
      },
    });
  });

  return (
    <section ref={scopeRef as any} className="relative w-full overflow-hidden bg-gradient-to-r from-purple-50 to-blue-50 py-24 md:py-32">
      <DotPattern className="opacity-30" />
      <div className="container px-4 md:px-6">
        <div className="mb-10 text-center">
          <h2 className="text-3xl font-bold tracking-tighter sm:text-5xl text-gradient">Why Students Love VoyLearning</h2>
        </div>
      </div>
      <div data-hscroll className="relative w-full md:overflow-visible overflow-x-auto">
        <div className="flex w-max items-stretch gap-8 px-6">
          {benefits.map((b, i) => (
            <GlassCard key={b.num} className="w-80 md:w-96 h-64 flex-shrink-0 hover:rotate-2 hover:shadow-premium-xl transition-transform" data-card>
              <CardContent className="h-full">
                <div className="relative h-full">
                  <div className="absolute right-2 top-1 text-7xl font-bold opacity-30 text-gradient select-none">{b.num}</div>
                  <div className="flex h-full flex-col justify-between">
                    <div className="flex items-center gap-3">
                      <div className="flex h-12 w-12 items-center justify-center rounded-full"
                           style={{ backgroundImage: 'var(--gradient-3)' }}>
                        <b.icon className="h-6 w-6 text-white" />
                      </div>
                      <CardTitle className="text-xl">{b.title}</CardTitle>
                    </div>
                    <p className="text-muted-foreground">{b.desc}</p>
                  </div>
                </div>
              </CardContent>
            </GlassCard>
          ))}
        </div>
      </div>
      <div className="container px-4 md:px-6 mt-6 text-center text-sm text-muted-foreground md:hidden">
        <p>On mobile, scroll horizontally to explore more benefits.</p>
      </div>
    </section>
  );
}
